.header-hambuger {
  @apply flex flex-col gap-2 ml-8 md:ml-11 lg:ml-11 xl:ml-[calc(58/19.2*1rem)] cursor-pointer z-10 relative xl:hidden;
}
.header-hambuger span {
  @apply transition-all duration-300 pointer-events-none;
  @apply w-7 h-1 bg-white relative z-10;
}
.header-hambuger span:nth-child(1) {
  transform-origin: center left;
}
.header-hambuger span:nth-child(2) {
  @apply opacity-100;
}
.header-hambuger span:nth-child(3) {
  transform-origin: center left;
}
.header-hambuger #pulseMe {
  transform-origin: center center;
  @apply pointer-events-none;
  @apply left-1/2 top-1/2 -translate-x-1/2 -translate-y-1/2 w-[38px] h-[38px] absolute;
}
.header-hambuger #pulseMe .bar {
  box-shadow: 0 0 0 #033EBC, 0 0 1px #033EBC, 0 0 1px #033EBC, 0 0 4px #033EBC;
  @apply absolute bg-white;
}
.header-hambuger #pulseMe .bar.left {
  animation: leftBar 2s linear infinite;
  @apply w-[1px];
}
.header-hambuger #pulseMe .bar.top {
  animation: topBar 2s linear infinite;
  @apply h-[1px] w-0;
}
.header-hambuger #pulseMe .bar.right {
  animation: rightBar 2s linear infinite;
  @apply w-[1px];
}
.header-hambuger #pulseMe .bar.bottom {
  animation: bottomBar 2s linear infinite;
  @apply h-[1px];
}

@keyframes leftBar {
  0% {
    height: 0;
    left: 0;
    top: 100%;
  }
  20% {
    height: 100%;
    left: 0;
    top: 0;
  }
  40% {
    height: 0;
    left: 0;
    top: 0;
  }
}
@keyframes topBar {
  0%, 20% {
    left: 0;
    top: 0;
    width: 0;
  }
  40% {
    left: 0;
    top: 0;
    width: 100%;
  }
  60% {
    left: calc(100% - 1px);
    top: 0;
    width: 0;
  }
}
@keyframes rightBar {
  0%, 40% {
    height: 0;
    left: calc(100% - 1px);
    top: 0;
  }
  60% {
    height: 100%;
    left: calc(100% - 1px);
    top: 0;
  }
  80% {
    height: 0;
    left: calc(100% - 1px);
    top: 100%;
  }
}
@keyframes bottomBar {
  0%, 60% {
    left: 100%;
    top: 100%;
    width: 0;
  }
  80% {
    left: 0;
    top: 100%;
    width: 100%;
  }
  100% {
    left: 0;
    top: 100%;
    width: 0;
  }
}
